export default {
    name: "qrcode",
    components: {},
    data() {
        return {
            setting: {},
        }
    },
    onLoad() { this.$commonLoad.onload();
        const self = this;
        self.$request({
            url: self.$api.lottery.setting,
        }).then(info => {
            if (info.code === 0) {
                self.setting = info.data.setting;
            }
        }).catch(e => {
            // 网络请求出错
        })
    },
    methods: {
        save(url) {
            const self = this;


            uni.showLoading({title: `图片保存中`});
            uni.downloadFile({
                url,
                success: function (e) {
                    uni.saveImageToPhotosAlbum({
                        filePath: e.tempFilePath,
                        success: function () {
                            uni.showToast({title: '保存成功'});
                        },
                        fail: function (err) {
                            if (err.errMsg) {
                                uni.showModal({
                                    title: '提示',
                                    content: '您好,请先授权保存到相册权限',
                                    showCancel: false,
                                    success(res) {
                                        if (res.confirm) {
                                            uni.openSetting({
                                                success(settingdata) {
                                                    if (settingdata.authSetting['scope.writePhotosAlbum']) {
                                                        uni.saveImageToPhotosAlbum({
                                                            filePath: e.tempFilePath,
                                                            success: function () {
                                                                uni.showToast({title: '保存成功'});
                                                            },
                                                        })
                                                    } else {
                                                        uni.showModal({
                                                            title: '提示',
                                                            content: '授权失败，请稍后重新获取',
                                                            showCancel: false,
                                                        })
                                                    }
                                                }
                                            })
                                        }
                                    }
                                })
                            }
                        },
                        complete: function (e) {
                            uni.hideLoading();
                        }
                    });
                },
                fail: function (e) {
                    uni.showModal({
                        title: '图片下载失败',
                        content: e.errMsg,
                        showCancel: false,
                    });
                },
                complete: function (e) {
                    uni.hideLoading();
                }
            });















        },

        copy() {
            this.$utils.uniCopy({
                data: this.setting.cs_wechat,
                success(){



                }
            });
        },
    }
}
